:root {
  --card-aspect-ratio: calc(838 / 1200);
}

html, body {
  color: #000;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  display: flex;
  position: relative;
}

#map {
  width: 100%;
  height: 100%;
  position: fixed;
}

#card-container {
  height: min(90%, 90vw / var(--card-aspect-ratio));
  max-width: 90%;
  max-height: 1200px;
  aspect-ratio: var(--card-aspect-ratio);
  perspective: 1000px;
  pointer-events: none;
  position: relative;
}

#card {
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  pointer-events: auto;
  visibility: hidden;
  border-radius: 4vh;
  transition: transform .3s;
  position: relative;
  box-shadow: 0 0 4vh #0009;
}

#card.flipped {
  pointer-events: none;
  transform: rotateY(180deg);
}

#inner-card-front, #inner-card-back {
  border-radius: inherit;
  backface-visibility: hidden;
  position: absolute;
  inset: 0;
}

#inner-card-front {
  background-image: url("card_blank.cc1bfbad.jpg");
  background-size: cover;
}

#inner-card-back {
  background-image: url("card_back.66a2d974.jpg");
  background-size: cover;
  transform: rotateY(180deg);
}

#category-id {
  font-size: min(6vh, 6vw / var(--card-aspect-ratio));
  color: #fff;
  -webkit-text-stroke: .25vh black;
  text-shadow: .5vh .5vh #0003;
  font-style: italic;
  font-weight: bolder;
  position: absolute;
  top: 3%;
  left: 14%;
  transform: translate(-50%);
}

#category {
  text-align: center;
  font-weight: bold;
  position: absolute;
  top: 7.5%;
  left: 27%;
  right: 6%;
}

#photo {
  aspect-ratio: 722 / 455;
  width: 86.5%;
  height: auto;
  position: absolute;
  top: 14.5%;
  left: 7%;
}

#location-name {
  text-align: center;
  font-size: min(3.5vh, 3.5vw / var(--card-aspect-ratio));
  font-style: italic;
  font-weight: light;
  font-family: Impact, sans-serif;
  position: absolute;
  top: 54.5%;
  left: 0;
  right: 0;
}

#description {
  font-size: min(1.5vh, 1.5vw / var(--card-aspect-ratio));
  font-style: italic;
  position: absolute;
  top: 60%;
  right: 9%;
}

#bezirkswappen {
  aspect-ratio: 125 / 163;
  filter: drop-shadow(.3vh .3vh #0003);
  width: 13%;
  max-width: 125px;
  position: absolute;
  top: 66%;
  left: 10.5%;
}

#plz {
  font-size: min(2vh, 2vw / var(--card-aspect-ratio));
  background: #fff;
  border: .25vh solid #000;
  border-radius: 50%;
  padding: .8vh;
  font-weight: bold;
  position: absolute;
  top: 78.5%;
  left: 12%;
  box-shadow: .3vh .3vh #0003;
}

#links {
  font-size: min(1.4vh, 1.4vw / var(--card-aspect-ratio));
  hyphens: auto;
  max-width: 17.5%;
  line-height: 1.2;
  position: absolute;
  bottom: 4.5%;
  left: 9%;
}

#links span {
  margin-top: .5vh;
  display: block;
}

.stat-label {
  font-size: min(1.5vh, 1.5vw / var(--card-aspect-ratio));
  font-style: italic;
  position: absolute;
  top: 66.5%;
  left: 42%;
}

#trend-label {
  margin-top: 10.5%;
}

#lkwratio-label {
  margin-top: 21%;
}

#lanes-label {
  margin-top: 32%;
}

.stat {
  font-size: min(3vh, 3vw / var(--card-aspect-ratio));
  font-style: italic;
  position: absolute;
  top: 66%;
  right: 8.5%;
}

#trend {
  margin-top: 10.5%;
}

#lkwratio {
  margin-top: 21.5%;
}

#lanes {
  margin-top: 33%;
}

.crosshair-icon {
  background: none;
  width: 30px;
  height: 30px;
  position: relative;
}

.crosshair-icon:before, .crosshair-icon:after {
  content: "";
  background-color: red;
  position: absolute;
  box-shadow: 0 0 8px #f009;
}

.crosshair-icon:before {
  width: 4px;
  height: 100%;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.crosshair-icon:after {
  width: 100%;
  height: 4px;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.marker-cluster span {
  color: #fff;
  font-weight: bold;
}

.marker-cluster-large, .marker-cluster-large div {
  background-color: #f00c !important;
}

.marker-cluster-medium, .marker-cluster-medium div {
  background-color: #f009 !important;
}

.marker-cluster-small, .marker-cluster-small div {
  background-color: #f006 !important;
}
